.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-left: 3px;
}

.card__description {
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  justify-content: space-between;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 17px;
  margin-left: 2px;
  display: flex;
}

.card__price {
  text-align: center;
  justify-content: space-between;
  padding-top: 23px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__description {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars :nth-child(n), .stars :nth-child(n+2), .stars :nth-child(n+3) {
  background-image: url("star-active.edda4c43.svg");
}

.stars :last-child {
  background-image: url("star.822d561e.svg");
  margin: 0;
}

.stars__text {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  font-family: Roboto, sans-serif;
  display: flex;
}

.logo-img {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: flex;
}

.nav {
  margin-right: 50px;
  display: block;
}

.nav__list {
  gap: 20px;
  height: 60px;
  margin: 0;
  padding: 0;
  line-height: 60px;
  display: flex;
}

.nav__item {
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  display: inline-block;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

* {
  box-sizing: border-box;
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}
